










import { Component, Vue } from 'vue-property-decorator'

@Component({
  components: {
  },
  })
export default class AppHeader extends Vue {
  toggleTheme () {
    this.$vua.dark = !this.$vua.dark
  }

  toggleAside () {
    let root = this.$root as any
    root.collapse = !root.collapse
  }
}
